import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import Layout from '../../components/Layout';

import '../../assets/scss/challenge.scss';



const Finish = (props) => {
   const pathname = useLocation().pathname.substring(1);

   if(pathname.startsWith('Study')) {
      return(
         <Layout header containName='finish study'>
            <strong className="finish-text">.<br/>  !</strong>
            <Link to="/Study/View" className="btn btn-line"> </Link>
            <Link to="/Challenge" className="btn btn-white"> </Link>
         </Layout>
      );
   } else if(pathname.startsWith('Challenge')) {
      return (
         <Layout header containName='finish challenge'>
            <strong className="finish-text"><br/> <i className="icon-flag"></i></strong>
            <p><span></span>   <span>1</span> .<br/>  </p>
            <Link to="/Challenge/Result" className="btn btn-white"> </Link>
         </Layout>
      );
   }
};



export default Finish;