import React, { useState } from 'react';
import Calendar from 'react-calendar';
import 'react-calendar/dist/Calendar.css';
import moment from "moment";

import Layout from '../components/Layout';
import Toparea from '../components/Toparea';

import '../assets/scss/main.scss'



const CheckCalendar = (props) => {
	const [value] = useState(new Date());

	//   
  const dayList = [
    '2024-03-10',
    '2024-03-21',
    '2024-04-02',
    '2024-04-14',
    '2024-04-27',
  ];

  //     
  const addContent = ({ date }: value) => {
    //  ()   
    const contents = [];

    // date( )      () 
    if (dayList.find((day) => day === moment(date).format('YYYY-MM-DD'))) {
      contents.push(
				<i className="icon-check" key={dayList}>
					<svg width="20" height="12" viewBox="0 0 20 12" fill="none" xmlns="http://www.w3.org/2000/svg">
						<path d="M1 6L5.5 10.5L19 1" stroke="white" strokeLinecap="round"/>
					</svg>
				</i>
      );
    }
    return <div>{contents}</div>; //     
  };


  return (
		<Layout containName='calendar'>
			<Toparea/>

			<div className="box-wrap">
				<div className="box-line">
					<div className="box calendar">
						<p></p>
						<strong><span>1</span></strong>
					</div>
				</div>
			</div>
			<Calendar
				calendarType="gregory" // 
				value={value}
				disabled={true}
				formatMonthYear={(locale, date) => moment(date).format("YYYY. MM")} //  yyyy.mm
				formatDay={(locale, date) => moment(date).format("D")} // 
				tileContent={addContent} //   
				showNeighboringMonth={false} // ,   
				next2Label={null} // +1   
				prev2Label={null} // -1   
				minDetail="year" // 10  
			></Calendar>

		</Layout>
	);
};

export default CheckCalendar;