import React, { useEffect } from 'react';
import Layout from '../../components/Layout';
import $ from "jquery";

const Find = (props) => {

  useEffect(() => {
    //select 
    $('.select button').text($('.select ul li:first-child').text());
  
    $('.select button').on('click', function(){
      var selectUl = $(this).next('ul')
      if(selectUl.is(':visible')) {
        selectUl.hide();
      } else {
        selectUl.show();
      }
    });
  
    $('.select li').on('click', function(){
      var selectText = $(this).text();
      $(this).parent('ul').siblings('button').text(selectText);
  
      if($('.select button').text() === '') {
        $('#email_direct').show().focus();
      } else {
        $('#email_direct').hide().blur();
      }
    });

  }, []);



  function step01() {
    $('.find-step01').find('.btn').hide();
    $('.find-step02').show();
  }

  function step02() {
    $("#authentication").prop('disabled', true);
    $('.find-step03').show();
  }


  return (
		<Layout header={{title:'', Backbtn:true, link:'/Login'}} containName='account'>

			<div className="find-step01">
        <div className="input-wrap">
          <label htmlFor="email"></label>
          <div className="input-group">
            <div className="input-wrap">
              <input type="text" id="email" placeholder=""/>
            </div>
            <div className="sign">@</div>
            <div className="input-wrap">
              <div className="select">
                <button type="button"></button>
                <ul>
                  <li>naver.com</li>
                  <li>hanmail.net</li>
                  <li>daum.net</li>
                  <li>kakao.com</li>
                  <li>gmail.com</li>
                  <li></li>
                </ul>
              </div>
            </div>
          </div>
          <input type="text" id="email_direct" placeholder=" "/>
        </div>

        <button type="button" className="btn" onClick={step01}> </button>
      </div>

      <div className="find-step02">
        <div className="input-group">
          <div className="input-wrap">
            <input type="text" id="authentication" placeholder=""/>
          </div>
          <button type="button" className="btn btn-black" onClick={step02}> </button>
        </div>
        <p className="text-guide text-right">  .<br/>  .</p>
        <p className="text-guide text-right text-colred">   .</p>
      </div>

      <div className="find-step03">
        <div className="input-wrap">
          <label htmlFor="pw_change"> </label>
          <input type="password" id="pw_change" placeholder="********"/>
        </div>

        <button type="button" className="btn"> </button>
      </div>

		</Layout>
  );
};

export default Find;